<template>
  <div class="queryImg">
    <div class="queryImgHeader">
       <img src="/images/queryImgHeader.png" alt />
    </div>
    <div class="ybox topBigImg">
        <van-swipe ref="vanSwipper" class="my-swipe" @change = "changeSwipper" :autoplay="6000" indicator-color="white">
            <van-swipe-item v-for="item in 4" :key = "item">
                 <img src="https://timgsa.baidu.com/timg?image&amp;quality=80&amp;size=b9999_10000&amp;sec=1596868394463&amp;di=8f6c1e7340672f91af850f642495681a&amp;imgtype=0&amp;src=http%3A%2F%2Fa4.att.hudong.com%2F63%2F32%2F16200000031302131735320471887.jpg"/>
            </van-swipe-item>
        </van-swipe>
      <div class="imgTitle">第1张（共5张）</div>
      <div class="imgList">
        <div @click="preImg" class="changeBtnLeft">
          <span class="iconfont icon-left2"></span>
        </div>
        <div class="imgItemW" id="imgItemW">
          <div v-for="(item,idx) in 4" @click="changeTo(idx)" :class="{'imgItem':true,'imgChecked':currentIdx ==idx,'unchecked':currentIdx !=idx }"  :key="idx">
            <img 
              src="https://timgsa.baidu.com/timg?image&amp;quality=80&amp;size=b9999_10000&amp;sec=1596868394463&amp;di=8f6c1e7340672f91af850f642495681a&amp;imgtype=0&amp;src=http%3A%2F%2Fa4.att.hudong.com%2F63%2F32%2F16200000031302131735320471887.jpg"
            />
            <div class="imgIdx">{{idx+1}}</div>
          </div>
        </div>
        <div class="changeBtnRight" @click="nextImg">
          <span class="iconfont icon-right"></span>
        </div>
      </div>
    </div>
    <div class="bottomTitle">长按照片保存或转发</div>
    <van-button type="info" class="queryImgBtn">查看其它违章</van-button>  
  </div>
</template>
<script>
export default {
  data() {
    return {
         currentIdx:1
    };
  },
  mounted() {
       
  },
  methods: {
    changeSwipper(idx){
         console.log(idx);
         this.currentIdx = idx;
    },
    changeTo(idx){
       let swipper = this.$refs.vanSwipper;
       swipper.swipeTo(idx);

    },
    nextImg() {
      console.log("下一张图片---");
       let swipper = this.$refs.vanSwipper;
       swipper.next();
    },

    preImg() {
       console.log("上一张图片----");
       let swipper = this.$refs.vanSwipper;
       swipper.prev(); 
    }
  }
};
</script>
